.verbal-question-card-main{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    font-size: 11px;
    border-radius: 4px;
    font-weight: 400;
    align-items: center;
   


    .verbal-time-delete{
        display: flex;
        width: 30%;
        justify-content: flex-end;
        align-items: center;
    }

    .verbal-text{
        width: 70%;
        cursor: pointer;
        font-size: 11px;
        font-family: 'Poppins', sans-serif;
    }

    .verbal-time{
     opacity: 0.5;
     padding: 0 10px;
     font-size: 11px;
     font-family: 'Poppins', sans-serif;
    

    }

    .verbal-delete{
        height: 25px;
        width: 25px;
        display: flex;
        justify-content: center;
        background: #fff;
        align-items: center;
        border-radius: 50%;
        cursor: pointer;
    }

    .verbal-edit{
        height: 25px;
        width: 25px;
        display: flex;
        justify-content: center;
        background: #d8dce2;
        align-items: center;
        border-radius: 50%;
        cursor: pointer;
    }
}